/**
 *
 * CbDatasourceAggregatorList
 *
 */

import React from 'react';

import { FormGroup, Col, Button, Card, CardBody } from 'reactstrap';
import DatasourceModel from 'cb-utils/models/datasource/DatasourceModel';
import DatasourceSelect from 'components/DatasourceSelect';
import { CbInputFieldColMargin } from 'components/CbInputField';

interface IProps {
  list: string[];
  options: DatasourceModel[];
  onChange: (list: string[]) => void;
}

interface IState {
  list: string[];
}

class CbDatasourceAggregatorList extends React.Component<IProps, IState> {
  state = {
    list: this.props.list,
  };

  render() {
    return (
      <Card>
        <CardBody>{this.renderList(this.state.list)}</CardBody>
      </Card>
    );
  }

  handleDatasourceChange(idx: number, e: React.ChangeEvent<HTMLInputElement>) {
    const newList = [
      ...this.state.list.slice(0, idx),
      e.target.value,
      ...this.state.list.slice(idx + 1),
    ];
    this.setState({
      ...this.state,
      list: newList,
    });
    this.props.onChange(newList);
  }

  handleDatasourceDelete(idx: number) {
    const newList = [
      ...this.state.list.slice(0, idx),
      ...this.state.list.slice(idx + 1),
    ];
    this.setState({
      ...this.state,
      list: newList,
    });
    this.props.onChange(newList);
  }

  handleDatasourceAdd = () => {
    const newId = this.props.options[0] ? this.props.options[0].id : '';
    const newList = [...this.state.list, newId];
    this.setState({
      ...this.state,
      list: newList,
    });
    this.props.onChange(newList);
  };

  renderList = (list: string[]) => {
    if (list.length > 0) {
      return (
        <div>
          {list.map(this.renderSelectBox)}
          <Button onClick={this.handleDatasourceAdd}>Add&nbsp; +</Button>
        </div>
      );
    } else {
      return <div>No datasources to choose</div>;
    }
  };

  renderSelectBox = (el: string, i: number) => {
    return (
      <FormGroup row key={i}>
        <DatasourceSelect
          labelText={`${i + 1})`}
          value={el}
          onChange={this.handleDatasourceChange.bind(this, i)}
          datasources={this.props.options.map((d) => ({
            name: d.name,
            value: d.id,
            preferred: false,
          }))}
          size={10}
          inlineSize="inherit"
        />
        <Col sm={2} style={CbInputFieldColMargin}>
          {this.renderDeleteButton(i)}
        </Col>
      </FormGroup>
    );
  };

  renderDeleteButton = (i: number) => {
    if (i > 0) {
      return (
        <Button onClick={this.handleDatasourceDelete.bind(this, i)}>
          &times;
        </Button>
      );
    }
  };
}

export default CbDatasourceAggregatorList;



