/**
 *
 * LatestDataModal
 *
 */

import React from 'react';
import styled from 'styled-components';

import PortalModel from '../../cb-utils/models/PortalModel';
import DatasourceModel from 'cb-utils/models/datasource/DatasourceModel';
import CbCodeEditor, { CbEditorLanguages } from '../CbCodeEditor';
import { FormattedMessage } from 'react-intl';
import messages from './messages';
const portalModel = PortalModel.getInstance();

const Wrapper = styled.div`
  height: 60vh;
`;

interface IProps {
  id: string;
}

interface IState {
  date: string;
  data: any;
}

const wrappingId = 'latestDataForDatasourceWrapper';

class LatestDataForDatasource extends React.Component<IProps, IState> {
  datasourceInstance: DatasourceModel;

  constructor(props: IProps) {
    super(props);

    this.datasourceInstance = portalModel.getDatasourceInstanceById(props.id);
    this.datasourceInstance.latestData.subscribe(this.handleUpdate);

    this.state = {
      ...this.pickStateFromModel(this.datasourceInstance),
    };
  }

  componentWillUnmount() {
    this.datasourceInstance.latestData.unsubscribe(this.handleUpdate);
  }

  render() {
    const { data } = this.state;
    const isDataOfTypeObject = typeof data === 'object';
    return (
      <div>
        {this.state.date ? (
          <React.Fragment>
            <h4 style={{ textAlign: 'center' }}>{this.state.date}:</h4>
            <Wrapper id={wrappingId}>
              <CbCodeEditor
                language={isDataOfTypeObject ? CbEditorLanguages.JSON : null}
                value={
                  isDataOfTypeObject ? JSON.stringify(data, null, 4) : data
                }
                onChange={() => {
                  return;
                }}
                wrappingId={wrappingId}
                sideBarWidth={0}
              />
            </Wrapper>
          </React.Fragment>
        ) : (
          <h6 className="text-center">
            <FormattedMessage {...messages.never} />
          </h6>
        )}
      </div>
    );
  }

  pickStateFromModel(model: DatasourceModel) {
    return {
      date: model.lastUpdated && model.lastUpdated.toLocaleString(),
      data: model.latestData(),
    };
  }

  handleUpdate = () => {
    this.setState({
      ...this.state,
      ...this.pickStateFromModel(this.datasourceInstance),
    });
  };
}

export default LatestDataForDatasource;



