/**
 *
 * CbPluginSettingEditor
 *
 */

import React from 'react';
// import styled from 'styled-components';

import CbInputField from 'components/CbInputField';

import MultiObjectSettingEditor from 'components/MultiObjectSettingEditor';

import { MULTI_OBJECT_SETTING_TYPE } from 'containers/Portal/constants';
import {
  PluginSettingDefinitionTypes,
  InputFieldProps,
  MultiObjectSettingDefinition,
} from 'utils/types';

const CbPluginSettingEditor: React.SFC<
  PluginSettingDefinitionTypes & InputFieldProps
> = (props) => {
  if (props.type === MULTI_OBJECT_SETTING_TYPE) {
    return (
      <MultiObjectSettingEditor {...props as MultiObjectSettingDefinition} />
    );
  } else {
    return <CbInputField {...props} labelText={props.display_name} />;
  }
};

CbPluginSettingEditor.propTypes = {};

export default CbPluginSettingEditor;



