/**
 *
 * DatasourceSettingsModal
 *
 */

import React, { FormEvent } from 'react';
// import styled from 'styled-components';

// import {FormattedMessage} from 'react-intl';
// import messages from './messages';

import {
  Modal,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Input,
  Label,
  Col,
  Row,
  Button,
} from 'reactstrap';

import CbPluginSettingEditor from '../../components/CbPluginSettingEditor';
import CbDatasourceAggregatorList from '../../components/CbDatasourceAggregatorList';
import { DATA_SETTING_TYPE } from '../../containers/Portal/constants';
import PortalModel from 'cb-utils/models/PortalModel';
import groupOptions from '../../cb-utils/groupOptions';
import allRequiredSettingsForPluginHaveData from '../../cb-utils/allRequiredSettingsForPluginHaveData';
import {
  CBEvent,
  DatasourceDefinition,
  PluginSettingDefinitions,
  PluginSettingDefinitionTypes,
  SettingsInstance,
} from 'utils/types';
import { Map, AnyMap } from 'cb-utils/console-entity-models';
import { DatasourceTypes } from 'plugins/datasources/types';
import FlexModalHeader from '../FlexModalHeader';
import ExportPluginButton from '../ExportPluginButton';
import ImportPluginButton from '../ImportPluginButton';
import messages from './messages';
import { isDatasourceFile, DatasourceSettingsModalTabs } from './utils';
import { IDatasourceInfo } from 'cb-utils/models/datasource/DatasourceModel';
import { injectIntl } from 'react-intl';
import DatasourceImportConfirmationModal from '../DatasourceImportConfirmationModal';
import uuid from 'uuid/v4';
import Tabs, { TabType } from 'components/Tabs';
import LatestDataForDatasource from '../LatestDataForDatasource';

export interface DatasourceSettingsModalReturn {
  id: string;
  isImport: boolean;
  importShouldCreateNewDatasource: boolean;
  type: DatasourceTypes;
  name: string;
  settings: AnyMap;
}

export interface DatasourceSettingsModalProps {
  name?: string;
  type?: DatasourceTypes;
  tab?: DatasourceSettingsModalTabs;
  settings?: SettingsInstance;
  id?: string;
  modalProps: {
    title: string;
  };
  modalType: string;
  onClose: (data: DatasourceSettingsModalReturn) => void;
  onDismiss: () => void;
  title: string;
}

interface DatasourceSettingsModalState {
  id: string;
  importedDatasource: IDatasourceInfo;
  isImport: boolean;
  importShouldCreateNewDatasource: boolean;
  datasourceType: DatasourceTypes;
  name: string;
  props: SettingsInstance;
  currentTab: DatasourceSettingsModalTabs;
}

const portalModel = PortalModel.getInstance();

class DatasourceSettingsModal extends React.Component<
  DatasourceSettingsModalProps & ReactIntl.InjectedIntlProps,
  DatasourceSettingsModalState
> {
  state: DatasourceSettingsModalState = {
    id: this.props.id || '',
    importedDatasource: null,
    isImport: false,
    importShouldCreateNewDatasource: false,
    name: this.props.name || '',
    // default to the topic datasource, should only happen when creating a new datasource
    datasourceType: this.props.type || DatasourceTypes.MESSAGE_TOPIC,
    props: { ...this.props.settings },
    currentTab: this.props.tab || DatasourceSettingsModalTabs.settings,
  };

  componentWillMount() {
    this.setState({
      props: this._setDefaultValuesForSettings(
        this.datasourceTypes[this.state.datasourceType].settings,
        this.state.props,
      ),
    });
  }

  datasourceTypes = portalModel.getDatasourceTypes();
  datasourceInstances = portalModel.getDatasourceInstances();

  render() {
    return (
      <Modal isOpen toggle={this.props.onDismiss} autoFocus={false}>
        <Form onSubmit={(e) => this._onSubmit(e)} data-cy="datasource-form">
          {this.props.id ? (
            <Tabs
              tabs={this.renderTabs()}
              currentTab={this.state.currentTab}
              selectNewTab={(currentTab: DatasourceSettingsModalTabs) => {
                this.setState({ currentTab });
                if (currentTab === DatasourceSettingsModalTabs.data) {
                  // if the user is navigating to the data tab we need to trigger a resize to get monaco-editor to fill the page properly
                  setTimeout(() => {
                    window.dispatchEvent(new Event('resize'));
                  });
                }
              }}
            />
          ) : (
            this.renderSettings()
          )}
        </Form>

        {this.state.importedDatasource && (
          <DatasourceImportConfirmationModal
            id={this.state.importedDatasource.id}
            onCreate={this.handleCreateFromImport}
            onReplace={this.handleReplaceFromImport}
            onCancel={this.handleCancelFromImport}
          />
        )}
      </Modal>
    );
  }

  renderSettings = () => {
    const exportObj: IDatasourceInfo = {
      id: this.props.id,
      name: this.state.name,
      settings: this.state.props,
      type: this.state.datasourceType,
    };
    return (
      <React.Fragment>
        <FlexModalHeader>
          {this.props.title}
          <div className="ml-auto">
            {this.props.id ? (
              <ExportPluginButton
                btnText={messages.exportDatasource}
                plugin={exportObj}
                fileName={this.state.name}
                helpInfo={messages.exportDatasourceHelp}
              />
            ) : (
              <ImportPluginButton
                btnText={messages.importDatasource}
                shapeChecker={isDatasourceFile}
                onComplete={this.handleImport}
                successMsg={this.props.intl.formatMessage(
                  messages.importDatasourceSuccess,
                )}
                badShapeMsg={this.props.intl.formatMessage(
                  messages.importDatasourceError,
                )}
                helpInfo={messages.importDatasourceHelp}
              />
            )}
          </div>
        </FlexModalHeader>

        <ModalBody>
          <FormGroup row>
            <Col sm={2}>
              <Label>Name</Label>
            </Col>
            <Col sm={10}>
              <Input
                data-cy="datasource-name"
                placeholder="Unique name..."
                value={this.state.name}
                autoFocus={this.state.name === ''}
                onChange={(e) => this._onSettingChange('name', e)}
              />
            </Col>
          </FormGroup>

          <FormGroup row>
            <Col sm={2}>
              <Label>Type</Label>
            </Col>
            <Col sm={10}>
              <Input
                type="select"
                placeholder="Select type..."
                value={this.state.datasourceType}
                onChange={(e) => this._onTypeChange(e)}
                data-cy="selectDatasourceType"
              >
                {this._renderDatasourceTypes(this.datasourceTypes)}
              </Input>
            </Col>
          </FormGroup>

          {this.datasourceTypes[this.state.datasourceType].description && (
            <Row>
              <Col sm={2}>
                <Label />
              </Col>
              <Col sm={10}>
                <span
                  className="text-muted font-italic"
                  style={{ fontSize: '0.8rem' }}
                >
                  {this.datasourceTypes[this.state.datasourceType].description}
                </span>
              </Col>
            </Row>
          )}
          <hr />
          {this._renderCustomProps(
            this.state.datasourceType,
            this.datasourceTypes[this.state.datasourceType].settings,
          )}
        </ModalBody>

        <ModalFooter>
          <Button color="default" onClick={() => this.props.onDismiss()}>
            Cancel
          </Button>
          <Button
            data-cy="save-ds"
            type="submit"
            color="primary"
            onClick={(e) => this._onSubmit(e)}
            disabled={!this._isFormComplete()}
          >
            Apply
          </Button>
        </ModalFooter>
      </React.Fragment>
    );
  };

  renderTabs = (): TabType[] => {
    return [
      {
        id: 'settings',
        title: 'Settings',
        component: this.renderSettings(),
      },
      {
        id: 'data',
        title: 'Data',
        component: this.renderDataView(),
      },
    ];
  };

  renderDataView = () => {
    return (
      <React.Fragment>
        <ModalBody>
          <LatestDataForDatasource id={this.props.id} />
        </ModalBody>

        <ModalFooter>
          <Button onClick={this.props.onDismiss}>Dismiss</Button>
        </ModalFooter>
      </React.Fragment>
    );
  };

  handleImport = (ds: IDatasourceInfo) => {
    // check to see if a datasource already exists with the imported ID
    if (portalModel.hasDatasourceWithId(ds.id)) {
      // it does exist - ask the user if they want to replace or create a new one
      this.setState({
        importedDatasource: ds,
      });
    } else {
      this.setImportValues(ds, true);
    }
  };

  setImportValues = (
    ds: IDatasourceInfo,
    importShouldCreateNewDatasource: boolean,
  ) => {
    this.setState({
      importedDatasource: null,
      importShouldCreateNewDatasource,
      isImport: true,
      id: ds.id,
      datasourceType: ds.type as DatasourceTypes,
      name: ds.name,
      props: ds.settings,
    });
  };

  handleCreateFromImport = () => {
    this.setImportValues(
      { ...this.state.importedDatasource, id: uuid() },
      true,
    );
  };

  handleReplaceFromImport = () => {
    this.setImportValues(this.state.importedDatasource, false);
  };

  handleCancelFromImport = () => {
    this.setState({
      importedDatasource: null,
      isImport: false,
    });
  };

  _handleAggregatorListChange = (val: string[]) => {
    this.setState({
      props: {
        ...this.state.props,
        datasources: val,
      },
    });
  };

  _isFormComplete = () => {
    return (
      this.state.name !== '' &&
      allRequiredSettingsForPluginHaveData(
        this.datasourceTypes[this.state.datasourceType].settings,
        this.state.props,
      )
    );
  };

  _onSettingChange = (key: keyof DatasourceSettingsModalState, e: CBEvent) => {
    this.setState({
      ...this.state,
      [key]: e.target.value,
    });
  };

  _onTypeChange = (e: CBEvent) => {
    const val = e.target.value;
    this.setState({
      props: this._setDefaultValuesForSettings(
        this.datasourceTypes[val].settings,
        {},
      ),
      datasourceType: val,
    });
  };

  _onDatasourcePropChange = (
    key: keyof PluginSettingDefinitionTypes,
    e: CBEvent,
  ) => {
    // key will eventually be setting for specific ds type
    this.setState({
      props: {
        ...this.state.props,
        [key]: e.target && e.target.value !== undefined ? e.target.value : e,
      },
    });
  };

  _onSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const rtn = {
      id: this.state.id,
      isImport: this.state.isImport,
      importShouldCreateNewDatasource: this.state
        .importShouldCreateNewDatasource,
      type: this.state.datasourceType,
      name: this.state.name,
      settings: this.state.props,
    };
    this.props.onClose(rtn);
  };

  _setDefaultValuesForSettings = (
    settingsForType: PluginSettingDefinitions,
    datasourceSettings: Partial<PluginSettingDefinitionTypes> | {},
  ) => {
    const rtn: Partial<PluginSettingDefinitionTypes> = {
      ...datasourceSettings,
    };

    settingsForType.forEach((setting: PluginSettingDefinitionTypes) => {
      if (
        rtn[setting.name as keyof PluginSettingDefinitionTypes] === undefined
      ) {
        if (setting.default_value !== undefined) {
          rtn[setting.name as keyof PluginSettingDefinitionTypes] =
            setting.default_value;
        }
      }
    });

    return rtn;
  };

  _getAggregatorOptions = () => {
    const rtn = [];
    for (const id in this.datasourceInstances) {
      if (id !== this.props.id) {
        rtn.push(this.datasourceInstances[id]);
      }
    }
    return rtn;
  };

  _getAggregatorList = () => {
    if (this.state.props.datasources && this.state.props.datasources.length) {
      return this.state.props.datasources;
    } else if (Object.keys(this.datasourceInstances).length > 0) {
      for (const id in this.datasourceInstances) {
        if (
          this.datasourceInstances.hasOwnProperty(id) &&
          id !== this.props.id
        ) {
          return [id];
        }
      }
    }
    return [];
  };

  _renderCustomProps = (
    type: DatasourceTypes,
    props: PluginSettingDefinitions,
  ) => {
    let rtn: any[] = [];
    if (props) {
      rtn = props.map((el: PluginSettingDefinitionTypes) => {
        if (el.type !== DATA_SETTING_TYPE) {
          return (
            <Row key={el.name}>
              <CbPluginSettingEditor
                {...el}
                size={12}
                onChange={(e) =>
                  this._onDatasourcePropChange(
                    el.name as keyof PluginSettingDefinitionTypes,
                    e,
                  )
                }
                value={
                  this.state.props[
                    el.name as keyof PluginSettingDefinitionTypes
                  ]
                }
              />
            </Row>
          );
        }
      });
    }
    if (type === DatasourceTypes.DATASOURCE_AGGREGATOR) {
      rtn.push(
        <CbDatasourceAggregatorList
          key="datasources"
          list={this._getAggregatorList()}
          options={this._getAggregatorOptions()}
          onChange={(newList: string[]) =>
            this._handleAggregatorListChange(newList)
          }
        />,
      );
    }
    return rtn;
  };

  _renderDatasourceTypes = (types: Map<DatasourceDefinition>) => {
    return groupOptions(types);
  };
}

export default injectIntl(DatasourceSettingsModal);



