/**
 *
 * DatasourceImportConfirmationModal
 *
 */

import React, { SFC } from 'react';
import { Modal, ModalBody, ModalFooter, Button } from 'reactstrap';
// import styled from 'styled-components';

import { FormattedMessage } from 'react-intl';
import messages from './messages';
import consoleMessages from 'containers/Console/messages';

interface DatasourceImportConfirmationModalProps {
  id: string;
  onCreate: () => void;
  onReplace: () => void;
  onCancel: () => void;
}

const DatasourceImportConfirmationModal: SFC<
  DatasourceImportConfirmationModalProps
> = (props) => {
  return (
    <Modal isOpen>
      <ModalBody>
        <h5>
          <FormattedMessage {...messages.mainText} values={{ id: props.id }} />
        </h5>
        <hr />
        <p>
          <em>
            <FormattedMessage {...messages.subText} />
          </em>
        </p>
      </ModalBody>
      <ModalFooter>
        <Button
          color="success"
          onClick={props.onCreate}
          data-cy="create-new-datasource"
        >
          <FormattedMessage {...messages.createBtnText} />
        </Button>
        <Button
          color="primary"
          onClick={props.onReplace}
          data-cy="replace-datasource"
        >
          <FormattedMessage {...messages.replaceBtnText} />
        </Button>
        <Button onClick={props.onCancel}>
          <FormattedMessage {...consoleMessages.cancel} />
        </Button>
      </ModalFooter>
    </Modal>
  );
};

export default DatasourceImportConfirmationModal;



